const screenSize = {
    ssm: 350,
    sm: 576,
    md: 768,
    lg: 992,
    xl: 1200,
    xxl: 1400,
  };
  
  export default (sizeName) => {
    const breakPointArray = Object.keys(screenSize).map((key) => [key, screenSize[key]]);
  
    const [result] = breakPointArray.reduce((acc, [name, size]) => {
      if (sizeName === name) return [...acc, `@media (max-width: ${size}px)`];
      return acc;
    }, []);
    return result;
  };
  