import React,{useState} from 'react'
import styled from '@emotion/styled'
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import breakPoint from '../utils/breakPoint'

import SwipeableDrawer from '@material-ui/core/SwipeableDrawer';
import clsx from 'clsx';
import { makeStyles } from '@material-ui/core/styles';
import Drawer from '@material-ui/core/Drawer';
import List from '@material-ui/core/List';
import Divider from '@material-ui/core/Divider';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import InboxIcon from '@material-ui/icons/MoveToInbox';
import MailIcon from '@material-ui/icons/Mail';
import PersonIcon from '@material-ui/icons/Person';
import EmojiObjectsIcon from '@material-ui/icons/EmojiObjects';
import ContactsIcon from '@material-ui/icons/Contacts';
import { NavLink } from 'react-router-dom'

const useStyles = makeStyles({
    list: {
      width: 250,
    },
    fullList: {
      width: 'auto',
    },
  });
function AppNavBar() {

    const classes = useStyles();
    const [openDrawer, setOpenDrawer] = useState(false) 

    const toggleDrawer = (open) => {
        setOpenDrawer(open);
    };

    const dataNavDrawer = [
        {Icon: PersonIcon, text: 'About'},
        {Icon: EmojiObjectsIcon, text: 'Projects'},
        {Icon: ContactsIcon, text: 'Contact'},
    ]
    
      const list = (anchor) => (
        <div
          className={clsx(classes.list, {
            [classes.fullList]: anchor === 'top' || anchor === 'bottom',
          })}
          role="presentation"
          onClick={()=>toggleDrawer(false)}
          onKeyDown={()=>toggleDrawer(false)}
        >
          <List>
            { dataNavDrawer.map(({Icon, text}, index) => (
              <ListItem button key={text}>
                <ListItemIcon><Icon /></ListItemIcon>
                <ListItemText primary={text} />
              </ListItem>
            ))}
          </List>
        </div>
      );


    const AppBarContainer = styled.div`
        .app-bar{
            background: transparent;
            .btn-nav:nth-of-type(1){
                margin-left:auto;
            }
            .btn-nav{
                margin-right:10px
            }
            .btn-drawer{
                display:none;
            }

        }
        ${breakPoint('sm')}{
            .btn-nav{
                display:none;
            }
            .btn-drawer{
                margin-left:auto;
                display:inline-flex !important;
            }
        }
    `

    return (
        <AppBarContainer>
            <AppBar position="sticky" className="app-bar">
                <Toolbar>
                    <Typography variant="h6">
                        Rian Mandala
                    </Typography>
                    <Button component={NavLink} to="#about" className="btn-nav" color="inherit">About</Button>
                    <Button component={NavLink} to="#project" className="btn-nav" color="inherit">Projects</Button>
                    <Button component={NavLink} to="#contact" className="btn-nav" color="inherit">Contact</Button>
                    <IconButton onClick={()=>toggleDrawer(true)} className="btn-drawer" color="inherit" aria-label="menu">
                        <MenuIcon />
                    </IconButton>

                    <SwipeableDrawer
                        anchor="left"
                        open={openDrawer}
                        onClose={()=> toggleDrawer(false)}
                        onOpen={()=>toggleDrawer(true)}
                        >
                        {list("left")}
                    </SwipeableDrawer>

                </Toolbar>
            </AppBar>
        </AppBarContainer>
    )
}

export default AppNavBar
