import styled from '@emotion/styled'
import React,{Fragment} from 'react'
import LinearProgress from '@material-ui/core/LinearProgress';
import photoProfil from '../assets/img/rian_pp.jpg'
function About() {

    const AboutContainer = styled.div`
        display: flex;
        padding:20px;
        margin-top:32px;
        justify-content: space-evenly;
        .about__info{
            width:45%;
            text-align:center;
            img{
                width:200px;
                height:200px;
                object-fit: cover;
                display:block;
                margin: 0 auto;
                border-radius:50%;
            }
            p{
                margin-top:32px;
                width: 70%;
                text-align:center;
                margin:32px auto;
                color: #616161;
            }
        }
        .about__skill{
            width:40%;
            .MuiLinearProgress-barColorPrimary{
                background-color: #009CA1;
            }
            .about__skill__body{
                margin-bottom: 12px;
            }
            .about__skill__info{
                display:flex;
                justify-content:space-between;
                color: #616161;
                margin-bottom:6px;
            }
        }
    `

    const skills = [
        {name: 'JavaScript (ES6)', number: 90},
        {name: 'HTML 5', number: 90},
        {name: 'CSS 3 ', number: 90},
        {name: 'SCSS', number: 90},
        {name: 'Bootstrap', number: 80},
        {name: 'React', number: 80},
        {name: 'PWA', number: 80},
        {name: 'Git', number: 80},
    ]

    return (
        <AboutContainer>
            <div className="about__info">
                <img src={photoProfil} alt="photo profil" />
                <p>I'am a Front-end web developer
                    from indonesia. I like to learn everthing about web 
                    technology</p>
            </div>
            <div className="about__skill">
                {
                    skills.map(skill=>(
                        <div className="about__skill__body">
                            <div className="about__skill__info">
                                <p>{skill.name}</p>
                                <p>{skill.number}</p>
                            </div>
                            <LinearProgress variant="determinate" value={skill.number} />
                        </div>
                    ))
                }
            </div>
        </AboutContainer>
    )
}

export default About
