import React from 'react'
import styled from '@emotion/styled'
import bgHeader from '../assets/img/bg_header.png'
import AppNavBar from './AppNavBar'
import avatarDesign from '../assets/img/avatar_design.svg'
import Button from '@material-ui/core/Button';

function Header() {

    const HeaderContainer = styled.div`
        background-image: linear-gradient(to bottom, rgba(37, 41, 52, 0.9), rgba(37, 41, 52, 0.9)), url('${bgHeader}');
        background-repeat:no-repeat;
        background-size:cover;
        .MuiPaper-elevation4{
            box-shadow: none;
        }
        .header__body{
            height: 80vh;
            display:flex;
            align-items: center;
            justify-content: center;
            .header__info{
                color:white;
                width:60%;
                h2{
                    text-align:left;
                    margin-bottom:4px;
                    font-weight: 700;
                    color: white;
                }
                .header-myname{
                    color: #E41D6F;
                }
                h1{
                    font-weight: 700;
                }
                .btn-download-cv{
                    color: white;
                    border-color: white;
                    padding: 10px 24px;
                    margin-top: 32px;
                }
            }
            img{
                width:30%;
            }
        }
    `

    return (
        <HeaderContainer>
            <AppNavBar />
            <div className="header__body">
                <div className="header__info">
                    <h2>Hello, <span className="header-myname">my name Rian mandala</span></h2>
                    <h1>I'am a front-end web developer</h1>

                    <Button className="btn-download-cv" variant="outlined">Download CV</Button>
                </div>
                <img src={avatarDesign} alt="avatar design" />
            </div>
        </HeaderContainer>
    )
}

export default Header
